/* Title */

@mixin title-default {
  color: var(--text-tale-title);
  font-family: var(--font-family);
  font-weight: 400;
  letter-spacing: -0.01em;
}

// Title on Desktop

@mixin h1-desk {
  @include title-default;
  font-size: 84px;
}

@mixin h2-desk {
  @include title-default;
  font-size: 54px;
}

@mixin h3-desk {
  @include title-default;
  font-size: 48px;
}

@mixin h4-desk {
  @include title-default;
  font-size: 36px;
}

@mixin h5-desk {
  @include title-default;
  font-size: 28px;
}

@mixin h6-desk {
  @include title-default;
  font-size: 24px;
}

// Title on Phone
// ...

/* Body Text */

@mixin body-text-default {
  color: var(--text-tale-title);
  font-family: var(--second-family);
  font-weight: 400;
  letter-spacing: -0.01em;
}

@mixin text-1-desk {
  @include body-text-default;
  font-size: 24px;
}

@mixin text-2-desk {
  @include body-text-default;
  font-size: 22px;
}

@mixin text-2b-desk {
  @include body-text-default;
  font-weight: 700;
  font-size: 22px;
}

@mixin text-4-desk {
  @include body-text-default;
  font-size: 18px;
}

@mixin text-4b-desk {
  @include body-text-default;
  font-weight: 700;
  font-size: 18px;
}

@mixin text-5-desk {
  @include body-text-default;
  font-size: 16px;
}

@mixin text-6-desk {
  @include body-text-default;
  font-size: 12px;
}

/* Other */
// ...


/* Typography classes */

._lowercase { text-transform: lowercase }
._uppercase { text-transform: uppercase }
._h1 { @include h1-desk }
._h2 { @include h2-desk }
._h3 { @include h3-desk }
._h4 { @include h4-desk }
._h5 { @include h5-desk }
._h6 { @include h6-desk }
